div.search {
  background-color: #fff;
}

div.search div.search-main {
  margin-top: 1.333333rem;
}

div.search div.search-main div.ss {
  padding-top: 0.266667rem;
  display: flex;
  justify-content: space-around;
}

div.search div.search-main div.ss input {
  padding: 0 0.533333rem;
  width: 7.733333rem;
  height: 0.8rem;
  line-height: 0.8rem;
  border: 0.026667rem solid #ccc;
  border-radius: 0.4rem;
}

div.search div.search-main div.ss button {
  width: 1.333333rem;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.373333rem;
  background-color: #db7093;
  color: #fff;
  border: none;
  border-radius: 0.133333rem;
}

div.search div.search-main div.hot {
  margin-top: 0.266667rem;
}

div.search div.search-main div.hot p {
  color: #ccc;
  font-size: 0.533333rem;
  padding: 0.266667rem;
}

div.search div.search-main div.hot div.hots {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

div.search div.search-main div.hot div.hots span {
  margin-left: 0.213333rem;
  display: inline-block;
  width: 1.76rem;
  height: 0.8rem;
  line-height: 0.8rem;
  background-color: #ccc;
  color: #000;
  text-align: center;
  margin-top: 0.133333rem;
  border-radius: 0.266667rem;
}

div.search div.search-main div.lsjl div.lisj-item {
  display: flex;
}

div.search div.search-main div.lsjl div.lisj-item p {
  color: #ccc;
  font-size: 0.533333rem;
  padding: 0.266667rem;
  align-self: flex-start;
}

div.search div.search-main div.lsjl div.lisj-item button {
  margin-left: 4.8rem;
  border: none;
  border-radius: 0.266667rem;
}

div.search div.search-main div.lsjl span {
  display: inline-block;
  text-align: center;
  line-height: 0.8rem;
  background-color: #ccc;
  border-radius: 0.4rem;
  width: 1.76rem;
  height: 0.8rem;
  line-height: 0.8rem;
  margin-left: 0.213333rem;
}